<template>
  <div class="news" style="width: 100%">
    <div id="menu">
      <ul>
        <li :class="{ active1: tabName == '头条'}" @click="setTab('头条')">头条</li>
        <li :class="{ active1: tabName == '新闻'}" @click="setTab('新闻')">新闻</li>
        <li :class="{ active1: tabName == '财经'}" @click="setTab('财经')">财经</li>
        <li :class="{ active1: tabName == '体育'}" @click="setTab('体育')">体育</li>
        <li :class="{ active1: tabName == '娱乐'}" @click="setTab('娱乐')">娱乐</li>
        <li :class="{ active1: tabName == '军事'}" @click="setTab('军事')">军事</li>
      </ul>
    </div>
    <news-tab :tab="tabName"></news-tab>
    <router-view></router-view>
    <div class="sidebar">
      <div class="back-top"><i class="backtop" style="display: none"></i></div>
    </div>
  </div>
</template>
<script>
// 引入axios
import Jquery from './jquery.min.js'
import NewsTab from './NewsTab/NewsTab.vue'
export default {
  data () {
    return {
      tabName: '头条',
      age: 18,
      toc: [],
      tabs: {
        toutiao: '头条',
        tiyu: '体育',
        shehui: '财经',
        guoji: '国际',
        guonei: '国内',
        yule: '娱乐'
      }
    }
  },
  methods: {
    setTab (tabName) {
      this.tabName = tabName
    }
  },
  created () {
  },
  components: {
    NewsTab
  }
}
$(function () {
  // 初始化图片的索引值为0
  var index = 0;
  // 克隆之后图片的总数量
  var len = $('.carousel-image li').length;
  // 定时器
  var timer;
  // 克隆第一张图片到末尾
  $('.carousel-image li:first').clone(true).appendTo($('.carousel-image'));
  run();

  // 1.自动轮播函数
  function run() {
    timer = setInterval(function () {
      //  console.log('len='+len+',index='+index)
      $('.carousel-index li:eq(' + index%len + ')').removeClass('active');

      // 到达最后一张图片
      if (index == len) {
        // 通过位置改变（margin-left）直接将图片列表移动到0的位置
        $('.carousel-image').css('margin-left', 0);
        // 位置改变了，索引值也跟着变化了
        index =0;
      }
      index++;

//      console.log(index);
      $('.carousel-index li:eq(' + index%len + ')').addClass('active');
      $('.carousel-image').animate({marginLeft:-375*index}, 200);
    }, 2000);
  }

  // 2. 鼠标移入
  $('.carousel').mouseenter(function () {
    // 停止定时任务
    clearInterval(timer);
  }).mouseleave(function () {
    // 继续轮播
    run();
  })

  // 3.鼠标移入索引值
  $('.carousel-index li').mouseenter(function () {
    $('.carousel-index li:eq(' + index + ')').removeClass('active');

    index = $(this).index();

    $('.carousel-index li:eq(' + index + ')').addClass('active');
    $('.carousel-image').stop(true).animate({marginLeft:-375*index}, 200);
  })
})
//返回顶部
$(function () {
  $(window).scroll(function () {
    if ($(this).scrollTop() > 510) {
      $('.backtop').show();
    } else {
      $('.backtop').hide();
    }
  });


  $('.backtop').click(function () {
    var top = $(window).scrollTop();
    var timer = setInterval(function () {
      top -= 10;

      if (top < 10) {
        top = 0;
        clearInterval(timer)
      }
      $(window).scrollTop(top)
    }, 1)
  })
})
</script>
<style lang="styl">
  .sidebar {
    position: fixed;
    z-index: 10;
    right: 0.3125rem;
    bottom: 6.25rem;
    width: 2.8125rem;
    padding: 0.3125rem 0.625rem;
    line-height: 1.5;
    font-size: 0.75rem;
    color: #666;
  }
  .back-top i {
    display: inline-block;
    width: 0.9375rem;
    height: 0.5rem;
    background-size: 13.125rem 10.9375rem;
    position: absolute;
    bottom: -1.25rem;
    right: 1.25rem;
    background: url(../assets/images/match-icon.png) no-repeat -9.9375rem -4.375rem;
  }

  #menu
    position fixed
    top 3.15625rem
    width 100%
    height 2.5rem
    background #5dabf0
    z-index 2
    ul
      width 100%
      height 100%
      display flex
      align-items center
      justify-content space-around
      li
        padding 0.1875rem 0.25rem
        color #fff
        font-size 0.875rem
        border-radius 0.1875rem
        &.active1
          background #369219
          color #FFF
  .carousel
    overflow hidden
    position relative
    width 23.4375rem
    height 17.5625rem
    margin 0.125rem auto
    .carousel-image
      display block
      li
        float left
        position relative
      img
        width 23.4375rem
        vertical-align middle
        display block
        min-height 17.4375rem
      p
        position absolute
        text-align left
        height 2.5rem
        font-size 1rem
        color #fff
        bottom 0
        left 0.625rem
        width 100%
        box-sizing border-box
          span
            display inline-block
            text-align left
            height 2.0rem
            line-height 2.0rem
            width 100%
            position absolute
            bottom 0
            font-size 1rem
    div
      position absolute
      top 9.375rem
      width 3.75rem
      height 3.75rem
      border-radius 50%
      line-height 3.75rem
      text-align center
      background yellow
    .carousel-index
      position absolute
      bottom 0.625rem
      right 0.625rem
      li
        float left
        width 0.625rem
        height 0.625rem
        margin-left 0.3125rem
        text-align center
        line-height 1.125rem
        background #ddd
        border-radius 50%
        &.active
          background #393e3e
</style>



