<template>
  <div id="tab">
    <!-- 放置loading组件 -->
    <loading v-if="!news.length"></loading>
    <!--<news-top :newsList="news"></news-top>-->
    <news-list :newsList="news"></news-list>
  </div>
</template>
<script>
import Loading from '../Loading'
import NewsList from './NewsList'
import NewsTop from './NewsTop'
import { getNewsList } from './getData/jsonpGetData.js'

export default {
  data () {
    // 定义板块对象
    return {
      news: [],
      img:[]
    }
  },
  // 挂载组件
  components: {
    Loading,
    NewsList,
    NewsTop
  },
  props: ['tab'],
  methods: {
    // 定义获取话题列表的方法
    _getNewsList (tab) {
      getNewsList({
        // 对应的是精华的话题
        channel: tab
      }).then((data) => {
        this.news = data.result.list
        this.img = data.result.list
        console.log(this.img)
      })
    }
  },

  // 声明周期-created指Vue进行实例化时的操作，只触发一次
  created () {
    // 页面初始化进入时，请求的选项
    this._getNewsList(this.tab)
  },

  // 监测某个值的变化
  watch: {
    tab () {
      // tab选项发生改变时，请求新的数据
      this._getNewsList(this.tab)
    }
  }
}

</script>
<style></style>



