<template>
  <div id="tab">
    <!-- 放置loading组件 -->
    <loading v-if="!topics.length"></loading>
    <list-item :topics="topics"></list-item>
  </div>
</template>
<script>
import Loading from '../Loading'
import ListItem from './ListItem'
// 引入请求数据的方法
import { getTopicList } from '../../getData/index'

export default {
  data () {
    // 定义板块对象
    return {
      topics: []
    }
  },
  // 挂载组件
  components: {
    Loading,
    ListItem
  },
  props: ['tab'],
  methods: {
    _getTopicList (tab) {
      getTopicList({
        // 对应的是精华的话题
        tab: tab
      }).then((data) => {
        this.topics = data.data.data
      })
    }
  },
  created () {
    this._getTopicList(this.tab)
  },
  watch: {
    tab () {
      this._getTopicList(this.tab)
    }
  }
}
</script>
<style></style>



