<template>
  <!-- 新闻列表 -->
  <div  id="NewsTop">
      <div class="carousel">
        <ul class="carousel-image" >
          <li v-if="newsList.length" v-bind:key="index" v-for="(item, index) in newsList">
            <a href="https://news.baidu.com/news#/detail/9578449000435534622/carousel">
              <img :src="item.pic" width="375px" alt="">
              <p class="optons-text"><span>{{item.title}}</span></p>
            </a>
          </li>
        </ul>
        <ol class="carousel-index">
          <li class="active"></li>
          <li></li>
          <li></li>
        </ol>
      </div>
    </div>
      <!--<div v-if="newsList.length" v-bind:key="index" v-for="(item, index) in newsList">-->
      <!--<div  id="item" >-->
        <!--<a href="">-->
            <!--&lt;!&ndash;第一张配图&ndash;&gt;-->
          <!--<div v-if="item.pic" class="new_img">-->
            <!--<img v-if="item.pic" v-lazy="item.pic">-->
          <!--</div>-->
          <!--<div class="content">-->
            <!--<router-link :to="{ name:'NewDes', params: {id:index, esr:item} }" class="title">-->
              <!--{{ item.title }}-->
            <!--</router-link>-->
            <!--<div class="new_des">-->
              <!--<span class="news-type">类别：{{ item.category }}新闻</span>-->
              <!--<span class="authorName">来源：{{ item.src }}</span>-->
              <!--<span class="news-date">日期：{{ item.time }}</span>-->
            <!--</div>-->
          <!--</div>-->
          <!--&lt;!&ndash;新闻的作者，分类，日期&ndash;&gt;-->
        <!--</a>-->

      <!--</div>-->
    <!--</div>-->
</template>
<script>
export default {
  data () {
    return {

    }
  },
  // 希望父级组件传递给子组件的数据
  props: ['newsList'],

  methods: {
  }
}
$(function () {
  // 初始化图片的索引值为0
  var index = 0;
  // 克隆之后图片的总数量
  var len = $('.carousel-image li').length;
  // 定时器
  var timer;
  // 克隆第一张图片到末尾
  $('.carousel-image li:first').clone(true).appendTo($('.carousel-image'));
  console.log(len)
  run();

  // 1.自动轮播函数
  function run() {
    timer = setInterval(function () {
      $('.carousel-index li:eq(' + index%len + ')').removeClass('active');

      // 到达最后一张图片
      if (index == len) {
        // 通过位置改变（margin-left）直接将图片列表移动到0的位置
        $('.carousel-image').css('margin-left', 0);
        // 位置改变了，索引值也跟着变化了
        index =0;
      }

      index++;

//      console.log(index);
      $('.carousel-index li:eq(' + index%len + ')').addClass('active');
      $('.carousel-image').animate({marginLeft:-375*index}, 200);
    }, 2000);
  }

  // 2. 鼠标移入
  $('.carousel').mouseenter(function () {
    // 停止定时任务
    clearInterval(timer);
  }).mouseleave(function () {
    // 继续轮播
    run();
  })

  // 3.鼠标移入索引值
  $('.carousel-index li').mouseenter(function () {
    $('.carousel-index li:eq(' + index + ')').removeClass('active');

    index = $(this).index();

    $('.carousel-index li:eq(' + index + ')').addClass('active');
    $('.carousel-image').stop(true).animate({marginLeft:-375*index}, 200);
  })
})
</script>
<style scope lang="styl">
  .carousel
    overflow hidden
    position relative
    width 23.4375rem
    height 17.5625rem
    margin 0.125rem auto
    .carousel-image
      display block
      li
        float left
        position relative
      img
        width 23.4375rem
        vertical-align middle
        display block
        min-height 17.4375rem
      p
        position absolute
        text-align left
        height 2.5rem
        font-size 1rem
        color #fff
        bottom 0
        left 0.625rem
        width 100%
        box-sizing border-box
          span
            display inline-block
            text-align left
            height 2.0rem
            line-height 2.0rem
            width 100%
            position absolute
            bottom 0
            font-size 1rem
    div
      position absolute
      top 9.375rem
      width 3.75rem
      height 3.75rem
      border-radius 50%
      line-height 3.75rem
      text-align center
      background yellow
    .carousel-index
      position absolute
      bottom 0.625rem
      right 0.625rem
      li
        float left
        width 0.625rem
        height 0.625rem
        margin-left 0.3125rem
        text-align center
        line-height 1.125rem
        background #ddd
        border-radius 50%
        &.active
          background #393e3e
</style>



