import jsonp from 'jsonp'
function getNewsList (params) {
  let newParams = Object.assign({
    // 默认参数
    channel: '头条',
    appkey: '60694e286d4b9c94',
    callback: 'query'
  }, params)
  // https://c.y.qq.com/v8/fcg-bin/v8.fcg?channel=singer&page=list&key=all_all_all&pagesize=100&pagenum=1&g_tk=5381&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0
  // http://v.juhe.cn/toutiao/index?key=e46601343821a414fce5db53c412e10d"
  // http://v.juhe.cn/toutiao/index?key=e46601343821a414fce5db53c412e10d
  // 目标结构 ?channel=singer&page=list&key=all_all_all&pagesize=100&pagenum=1&g_tk=5381&loginUin=0&hostUin=0&format=jsonp&inCharset=utf8&outCharset=utf-8&notice=0&platform=yqq&needNewCode=0
  let paramStr = '?'
  for (let i in newParams) {
    // console.log(i);
    paramStr += i + '=' + newParams[i] + '&'
  }

  //  console.log(paramStr)
  //  请求数据的url地址
  let url = 'http://api.jisuapi.com/news/get' + paramStr
  // console.log(123,url)
  // 将普通函数改写为peomise函数
  return new Promise(function (resolve, reject) {
    // resolve 表示成功
    // reject 表示失败
    // 发送请求
    jsonp(url, {
      // 约定的jsonp参数名
      param: 'jsonpCallback',
      // 前缀
      prefix: '',
      // jsonp参数的值
      name: 'query'
    }, (err, data) => {
      // err 如果成功了，err值null
      if (!err) {
        resolve(data)
      } else {
        console.log('jsonp请求失败了')
      }
    })
  })
}
function search (param) {
  console.log('进入方法')
  let newParams = Object.assign({
    // 默认参数
    keyword: '头条',
    appkey: '60694e286d4b9c94',
    callback: 'query'
  }, param)
  let paramStr = '?'
  for (let i in newParams) {
    // console.log(i);
    paramStr += i + '=' + newParams[i] + '&'
  }

  let baseURL = 'http://api.jisuapi.com/news/search'+ paramStr
  return new Promise(function (resolve, reject) {
    // resolve 表示成功
    // reject 表示失败
    // 发送请求
    jsonp(baseURL, {
      // 约定的jsonp参数名
      param: 'jsonpCallback',
      // 前缀
      prefix: '',
      // jsonp参数的值
      name: 'query'
    }, (err, data) => {
      // err 如果成功了，err值null
      if (!err) {
        console.log(data.result.list)
        // this.listItem = data.result.list
        resolve(data)
      } else {
        console.log('jsonp请求失败了')
      }
    })
  })
}
export { getNewsList, search }



