import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Our from '@/components/Our'
import News from '@/components/News'
import Login from '@/components/my/Login.vue'
import Reg from '@/components/my/Reg.vue'
import Search from '@/components/Search.vue'
import Topic from '@/components/index/Topic'
import NewDes from '@/components/index/NewDes'
import SeaDes from '@/components/index/SeaDes'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'News',
      component: News
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index,
      children:[
        // 话题详情
        {
          path: 'Topic/:id',
          name: 'Topic',
          component: Topic
        }
      ]
    },
    {
      path: '/Search',
      name: 'Search',
      component: Search,
      children:[
        // 新闻详情
        {
          path: 'SeaDes/:id',
          name: 'SeaDes',
          component: SeaDes
        }
      ]
    },
    {
      path: '/News',
      name: 'News',
      component: News,
      children:[
        // 新闻详情
        {
          path: 'NewDes/:id',
          name: 'NewDes',
          component: NewDes
        }
      ]
    },
    {
      path: '/Our',
      name: 'Our',
      component: Our,
      children: [
        {
          path: '/Our',
          name: 'Reg',
          component: Reg
        },
        {
          path: '/Our/Login',
          name: 'Login',
          component: Login
        }
      ]
    }
  ]
})



