<template>
  <transition  name="top">
    <div>
      <div id="content-top"></div>
      <div id="topic">
        <button @click="back" id="back">&lt;</button>
        <div v-if="topic" class="markdown-text">
          <div v-html="topic.content"></div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import {getTopicDetail} from '../../getData/detail'
export default {
  data() {
    return {
      topic: null
    }
  },
  methods: {
    back() {
      this.$router.back()
    }
  },
  created() {
    let id = this.$route.params.id
    getTopicDetail(id).then((data) => {
      this.topic = data.data.data
      console.log(this.topic);
    })
  }
}
</script>
<style lang="styl">
  .top-enter
    opacity 0
  .top-enter-active
    transition opacity 0.3s linear
  .top-enter-to
    opacity 1
  #content-top
    width 100%
    height 6.35rem

  #topic
    width 100%
    min-height 90.0rem
    background #ccc
    position absolute
    top 0px
    left 0
    z-index 2
    overflow hidden
    #back
      width 4.25rem
      height 1.875rem
      font-size 1.25rem
      border-radius 40%
      border none
      font-family iconfont
      margin 0.3125rem
    .markdown-text
      line-height 2em
      padding 10px
      background-color #fff
      border-radius 0 0 3px 3px
    .markdown-text>:last-child, .preview>:last-child
      margin-bottom 1em
      font-size 14px
      border-radius 0
      padding 0 15px
      border 0
      margin 20px -10px
      border-width 1px 0
      p
       font-size 15px
       line-height 1.7em
       overflow auto
       a
        color #778087
       img
        height auto
        max-width 100%
        vertical-align middle
        border 0
        code
        white-space pre-wrap
        a
          color #778087
    h3
      border-bottom 1px solid #eee
      margin 30px 0 15px
      font-size 24.5px
      line-height 40px
      font-weight 700
    h2
      border-bottom 1px solid #eee
      margin 30px 0 15px
      font-size 24.5px
      line-height 40px
      font-weight 700
    h1
      border-bottom 1px solid #eee
      margin 30px 0 15px
      font-size 24.5px
      line-height 40px
      font-weight 700
    h4
      border-bottom 1px solid #eee
      margin 30px 0 15px
      font-size 24.5px
      line-height 40px
      font-weight 700
</style>



