<template>
  <transition  name="top">
    <div>
      <div id="content-top"></div>
      <div id="topic1">
        <button @click="back" id="back">返回</button>
        <div class="art_box">
          <div class="art_tit_h1">
            {{rees.title }}
          </div>
          <h2 class="weibo_user" style="background: #fff;padding: 0.9375rem 0.9375rem 0.625rem;">{{rees.src}}</h2>
          <h2 class="time" style="background: #fff;padding: 0.9375rem 0.9375rem 0.625rem;"><span class="weibo_time">{{rees.time}}</span></h2>
          <div class="art_p" v-html="rees.content">{{ rees.content }}</div>
          <img :src="rees.pic">
        </div>
      </div>
      <div class="sidebar">
        <div class="back-top"><a href="#back"><i class="backtop" style="display: none"></i></a></div>
      </div>
    </div>
  </transition>
</template>
<script>
import Jquery from '../jquery.min.js'
import { getNewsList } from '@/components/NewsTab/getData/jsonpGetData'
export default {
  data (){
    return {
     rees : {}
    }
  },
  methods: {
    back () {
      this.$router.back()
    }
  },
  created () {
    this.rees = this.$route.params.esr
    console.log(this.rees)
  }
}
//返回顶部
$(function () {
  $(window).scroll(function () {
    if ($(this).scrollTop() > 510) {
      $('.backtop').show();
    } else {
      $('.backtop').hide();
    }
  });


  $('.backtop').click(function () {
    var top = $(window).scrollTop();
    var timer = setInterval(function () {
      top -= 10;

      if (top < 10) {
        top = 0;
        clearInterval(timer)
      }
      $(window).scrollTop(top)
    }, 1)
  })
})
</script>
<style lang="styl">
  .top-enter
    opacity 0
  .top-enter-active
    transition opacity 0.3s linear
  .top-enter-to
    opacity 1
  #content-top
    width 100%
    height 6.35rem

  #topic1
    width 100%
    min-height 999rem
    background #f0f0f0
    position absolute
    top 0px
    left 0
    z-index 2
    overflow hidden
    #back
      width 4.25rem
      border-radius 40%
      height 1.875rem
      font-size 1.25rem
      border none
      font-family iconfont
      margin 0.3125rem

      img
        width 100%
    .art_box
      width 100%
      background #f0f0f0
      .art_tit_h1 {
        font-size: 24px;
        color: #1a1a1a;
        line-height: 32px;
        -o-text-overflow: ellipsis;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        word-break: break-all;
        font-weight: 700;
        text-align: justify;
        background: #fff;
        position: relative;
        padding: 15px 15px 10px;
        box-sizing: border-box;
      }
      .weibo_time {
        font-size: 12px;
        color: #888;
        line-height: 12px;
      }
      .weibo_user {
        float: none;
        font-size: 12px;
        width: auto;
        vertical-align: middle;
        font-weight: 400;
        color: #333;
        line-height: 0px;
        padding-top: 20px;
      }
      .art_p {
        font-size: 20px;
        color: #1a1a1a;
        line-height: 40px;
        word-break: normal;
        text-align: justify;
        padding: 0px 4px 0px;
        box-sizing: border-box;
      }
      .art_p p {
        margin: 10px 10px;
      }
      img {
        width: 100%;
        vertical-align: middle;
      }
</style>



