<template>
  <div>
    <div class="carousel">
      <ul class="carousel-image">
        <li v-if="newsList.length" v-bind:key="index" v-for="(item, index) in newsList">
          <a v-if="item.pic" href="">
            <router-link :to="{ name:'NewDes', params: {id:index, esr:item} }">
              <img :src="item.pic" width="375px" alt="">
            </router-link>
            <p class="optons-text"><span>{{item.title}}</span></p>
          </a>
        </li>
        <li></li>
        <li></li>
      </ul>
      <ol class="carousel-index">
        <li class="active"></li>
        <li></li>
        <li></li>
      </ol>
    </div>
    <!-- 新闻列表 -->
    <div  id="NewsList">
        <div v-if="newsList.length" v-bind:key="index" v-for="(item, index) in newsList">
        <div  id="item" >
          <a href="">
              <!--第一张配图-->
            <div v-if="item.pic" class="new_img">
              <img v-if="item.pic" v-lazy="item.pic">
            </div>
            <div class="content">
              <router-link :to="{ name:'NewDes', params: {id:index, esr:item} }" class="title">
                {{ item.title }}
              </router-link>
              <div class="new_des">
                <span class="news-type">类别：{{ item.category }}</span>
                <span class="authorName">来源：{{ item.src }}</span>
                <span class="news-date">日期：{{ item.time }}</span>
              </div>
            </div>
            <!--新闻的作者，分类，日期-->
          </a>

        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {

    }
  },
  // 希望父级组件传递给子组件的数据
  props: ['newsList'],

  methods: {
  }
}
</script>
<style scope lang="styl">
  #NewsList {
    margin 0.9375rem 1.0625rem 4.375rem
  }
  #item {
    width: 100%;
  }
  #item>a {
    width: 100%;
    display: flex;
    padding: 0.625rem  0;
    position: relative;
    overflow: hidden;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  .content {
    margin-left: 0.875rem;
    display: -webkit-box;
    float: right;
    display: flex;
    flex-direction: column;
    align-content: space-around;
  }
  .title {
    color: #333;
  }

  .new_des {
    display: block;
    color: #999;
    font-size: .13rem;
    margin-right: 0.3125rem;
    font-weight: 400;
    float: left;

  }

  .new_img {
    width:100% ;
    float: left;
    margin-left: 0.0625rem;
    font-size: 0;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    background-color: #F4F7F9;
    position: relative;
  }

  .new_img > img {
    height: auto;
    width: 100%;
    vertical-align: middle;
  }
</style>



