<template>
  <div id="search_view" class="">
    <div class="search-box-container">
      <div class="box-container">
        <input type="search" value="" v-model="searchVal"><!--v-model="searchVal"-->
      </div>
      <div class="btn"><span  @click="_search()">搜索新闻</span></div>
    </div>
    <!-- 放置loading组件 -->
    <loading v-if="!lod.length"></loading>
    <div v-if="listItem.length" id="item" v-bind:key="index" v-for="(item, index) in listItem">
        <a :href="item.url">
          <!--第一张配图-->
          <div v-if="item.pic" class="new_img">
            <img v-if="item.pic" v-lazy="item.pic">
          </div>
          <div class="content">
            <router-link :to="{ name:'SeaDes', params: {id:index, esr:item} }" class="title">
              {{ item.title }}
            </router-link>
            <div class="new_des">
              <span class="news-type">类别：{{ item.category }}新闻</span>
              <span class="authorName">来源：{{ item.src }}</span>
              <span class="news-date">日期：{{ item.time }}</span>
            </div>
          </div>
          <!--新闻的作者，分类，日期-->
        </a>

      </div>
    <router-view></router-view>
  </div>
</template>
<script>
import Loading from './Loading'
import { search } from './NewsTab/getData/jsonpGetData.js'
export default {
  data () {
    return {
      listItem: [],
      lod:['a'],
      searchVal: ''
    }
  },
  components: {
    Loading
  },
  methods: {
    _search () {
      this.lod = []
        let _this = this
      _this.listItem = []
      // 定义根据输入内容搜索新闻的方法
      search({
        // 对应的是输入的内容
        keyword: _this.searchVal
      }).then((data) => {
        this.listItem = data.result.list
        this.lod = ['a']
        console.log(this.listItem)
      })
    }
  },
  creat () {

  },
  computed: {
  }
}
</script>
<style scope lang="styl">
  #search_view {
    margin 0.9375rem 1.0625rem 4.375rem
  }
  #item {
    width: 100%;
  }
  #item>a {
    width: 100%;
    display: flex;
    padding: 0.625rem  0;
    position: relative;
    overflow: hidden;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }
  .content {
    margin-left: 0.875rem;
    display: -webkit-box;
    float: right;
    display: flex;
    flex-direction: column;
    align-content: space-around;
  }
  .title {
    color: #333;
    margin-bottom: 0.625rem;
  }

  .new_des {
    display: block;
    color: #999;
    font-size: .13rem;
    margin-right: 0.3125rem;
    font-weight: 400;
    float: left;

  }

  .new_img {
    width:100% ;
    float: left;
    margin-left: 0.0625rem;
    font-size: 0;
    vertical-align: middle;
    text-align: center;
    overflow: hidden;
    background-color: #F4F7F9;
    position: relative;
  }

  .new_img > img {
    height: auto;
    width: 100%;
    vertical-align: middle;
  }
  .search-box-container {
    margin:  0.9375rem 1.0625rem 0;
    position: relative;
    z-index: 1;
    height: 2.75rem;
    line-height: 2.75rem;
    border: 0.0625rem solid #ececec;
  }
  .search-box-container .box-container input[type=search] {
    height: 2.75rem;
    padding: 0 2.8125rem 0 0;
    top: 0;
    left: 0.25rem;
    right: 2.5rem;
    border: 0;
    border-radius: 0;
    background: 0 0;
    -webkit-appearance: none;
    font-size: 1.0rem;
   text-indent: 1.0rem;
    vertical-align: middle;
    outline: none;
  }
  .box-container {
    width: 70%;
  }
  .search-box-container .btn {
    position: absolute;
    top: 0;
    right: 0;
    margin-left: 0;
    border-left: 0.0625rem solid #ececec;
    z-index: 0;
    background: #fcfcfc;
    overflow: hidden;
    line-height:0.875rem ;
    width:5.1875rem ;
    text-align: center;
    font-size: 0.875rem;
    padding: 0.9375rem 0;
  }
</style>



