<template>
  <transition  name="top">
    <div>
      <div id="content-top"></div>
      <div id="topic">
        <button @click="back" id="back">返回</button>
        <div class="art_box">
          <div class="art_tit_h1">
            {{rees.title }}
          </div>
          <h2 class="weibo_user" style="background: #fff;padding: 0.9375rem 0.9375rem 0.625rem;margin: 0">{{rees.src}}</h2>
          <h2 class="time" style="background: #fff;padding: 0.9375rem 0.9375rem 0.625rem;margin: 0"><span class="weibo_time">{{rees.time}}</span></h2>
          <div class="art_p" v-html="rees.content">{{ rees.content }}</div>
          <img :src="rees.pic">
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import Jquery from '../jquery.min.js'
import { getNewsList } from '@/components/NewsTab/getData/jsonpGetData'
export default {
  data (){
    return {
     rees : {}
    }
  },
  methods: {
    back () {
      this.$router.back()
    }
  },
  created () {
    this.rees = this.$route.params.esr
    console.log(this.rees)
  }
}
</script>
<style lang="styl">
  .top-enter
    opacity 0
  .top-enter-active
    transition opacity 0.3s linear
  .top-enter-to
    opacity 1
  #content-top
    width 100%
    height 6.35rem

  #topic
    width 100%
    min-height 999rem
    background #f0f0f0
    position absolute
    top 0
    left 0
    z-index 2
    overflow hidden
    #back
      width 4.25rem
      border-radius 40%
      height 1.875rem
      font-size 1.25rem
      border none
      font-family iconfont
      margin 0.3125rem

      img
        width 100%
    .art_box
      width 100%
      background #f0f0f0
      .art_tit_h1
        font-size 1.5rem
        color #1a1a1a
        line-height2.0rem
        -o-text-overflow ellipsis
        text-overflow ellipsis
        display -webkit-box
        -webkit-line-clamp 2
        -webkit-box-orient vertical
        word-break break-all
        font-weight 700
        text-align justify
        background #fff
        position relative
        padding 0.9375rem 0.9375rem 0.625rem
        box-sizing border-box
      .weibo_time
        font-size 0.75rem
        color #888
        line-height 0.75rem
      .weibo_user
        float none
        font-size 0.75rem
        width auto
        vertical-align middle
        font-weight 400
        color #333
        line-height 0
        padding-top 1.25rem
      .art_p
        font-size 1.25rem
        color #1a1a1a
        line-height 2.5rem
        word-break normal
        text-align justify
        padding 0 0.25rem 0
        box-sizing border-box
      .art_p p
        margin 0.625rem 0.625rem
      img
        width 100%
        vertical-align middle
</style>



