<template>
  <div id="app">
    <!-- 定义头部 -->
    <div id="header">
      <router-link ref="" to="/Our">
        <img class="float-t profile-icon">
      </router-link>
      <!-- @符号指向了src目录 -->
      <img src="./assets/logo.png" alt="">
      <!--<div id="title">Vue.js</div>-->
      <router-link to="/Search">
        <img class="float-right search-icon">
      </router-link>
    </div>
    <!-- 显示内容区域 -->
    <div class="content-top"></div>
    <div id="content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>

    <!-- 分为两个模块：首页、我的 -->
    <div id="nav">
      <router-link to="/Index">首页</router-link>
      <router-link to="/News">新闻</router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

<!-- 定义style采用的css预处理器 -->
<style lang="styl">
  /* 引入其他的CSS文件 */
  @import './assets/css/reset.styl';

  /* 当前页面内的样式 */
  .content-top {
    width: 100%;
    height: 5.5rem;
  }
  .search-icon {
    border: none;
    width: 2.125rem;
    height: 100%;
    background: url(./assets/images/icon.png) no-repeat;
    background-size: 2.5rem 31.25rem;
    background-position: center -10.375rem;
    margin-right: 0.625rem;
  }
  .profile-icon {
    display: block;
    width: 2.125rem;
    height: 100%;
    background: url(./assets/images/icon.png) no-repeat;
    background-size: 2.5rem 31.25rem;
    background-position: center -29.25rem;
  }
  #header
    position fixed
    z-index 2
    width 100%
    height 3.125rem
    background #5dabf0
    display flex
    justify-content space-around
    align-items center
    border-bottom 0.03125rem solid #fff
    img
      display block
      height 1.875rem
    #title
      font-size 1.25rem
      /*font-weight: bold;*/
      color #FFF
      margin-left 0.3125rem
      /* & 表示一个引用，引用自身 */
      &:hover
        color $bgColor
  #nav
    width 100%
    height 3.125rem
    background #5dabf0
    position fixed
    bottom 0
    display flex
    justify-content center
    a
      display block
      width 100%
      height 100%
      text-align center
      line-height 3.125rem
      color #FFF
      &.router-link-active
        color #369219
</style>



