<template>
  <div id="normal_login" style="display: block;">
    <div class="img-div">
      <div class="logo-dom"></div>
    </div>
    <div class="phone_num_box">
      <input class="input" id="normal_login_phone_num" type="text" placeholder="用户名/手机号*">
    </div>
    <div class="m_password_box">
      <input class="input" id="normal_phone_pass" type="password" placeholder="请输入密码*">
    </div>
    <div id="m_normal_logBtn">
      登&nbsp;&nbsp;&nbsp;录
    </div>
    <div class="m_msg_box clearfix">
      <input class="checkbox" type="checkbox" name="" id="checkbox_02" value="">
      <span class="bottom_msg">
            <span>我已同意并阅读</span>
            <span class="zhuce">«News官方网站用户注册协议»</span>
        </span>
      <a href="findPassword.html" class="fogetPassword">忘记密码</a>
    </div>
    <div class="m_notice_msg_02"></div>
  </div>
</template>
<script>
export default {
  name: 'normal_login'
}
</script>
<style>
  .img-div {
    width: 100%;
    height: 2.133333333333333rem;
    margin-top: .933333333333333rem;
  }
  .logo-dom {
    width: 3.68rem;
    height: 1.226666666666667rem;
    margin: 0 auto;
    /*background: url(../assets/images/logo.png) no-repeat;*/
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
  }
  .clear::after {
    content: '';
    display: block;
    clear: both;
  }
  *, .log_content {
    margin: 0 auto;
  }
  input, select, textarea {
    outline: none;
    border-radius: 0;
  }
  body {
    font-family: Times New Roman,\\5B8B\4F53;
  }
  .log_content h3 {
    text-align: center;
    margin: 4.375rem 0 3.125rem;
    background: url(../../assets/images/1.svg) no-repeat 50%;
    height: 2.6rem;
  }
  #normal_login {
    width: 86%;
    margin: 0 auto;
  }
  #m_normal_logBtn,#normal_login .input {
    width: 99%;
    height: 2.375rem;
    border: 1px solid #e2e2e2;
  }
  #normal_login .input {
    margin-bottom: 0.625rem;
    text-indent: 1.0rem;
  }
  #m_normal_logBtn {
    border-radius: 0.25rem;
    text-align: center;
    line-height: 2.5rem;
    margin-top: 0.75rem;
    font-size: 0.875rem;
    color: #53565a;
  }
  .m_msg_box {
    margin-top: 12px;
    color: #b3b3b3;
  }
  .checkbox {
    margin-top: 0.75rem;
    margin-right: 0.3125rem;
  }
  .bottom_msg {
    color: #b3b3b3;
    font-size: 0.8125rem;
  }
  .fogetPassword {
    font-size: 0.8125rem;
    color: silver;
    display: block;
    margin-top: 0.9375rem;
  }
</style>



